import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiObjectDetectionInput = {
  image: number[];
};

export type AiObjectDetectionOutput = {
  score?: number;
  label?: string;
}[];

export class AiObjectDetection implements AiTask {
  private modelSettings: any;
  public inputs: AiObjectDetectionInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiObjectDetectionOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      oneOf: [
        { type: "string", format: "binary" },
        {
          type: "object",
          properties: {
            image: {
              type: "array",
              items: {
                type: "number",
              },
            },
          },
        },
      ],
    },
    output: {
      type: "array",
      contentType: "application/json",
      items: {
        type: "object",
        properties: {
          score: {
            type: "number",
          },
          label: {
            type: "string",
          },
          box: {
            type: "object",
            properties: {
              xmin: {
                type: "number",
              },
              ymin: {
                type: "number",
              },
              xmax: {
                type: "number",
              },
              ymax: {
                type: "number",
              },
            },
          },
        },
      },
    },
  };

  constructor(inputs: AiObjectDetectionInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.Uint8, this.preProcessedInputs.image, {
        shape: [1, this.preProcessedInputs.image.length],
        name: "input",
      }),
    ];
  }

  postProcessing(response: any) {
    const scores = response.scores.value[0].map((score: any, i: number) => {
      return {
        score: score,
        label: response.name.value[response.labels.value[0][i]],
        box: {
          xmin: response.boxes.value[0][i][0],
          ymin: response.boxes.value[0][i][1],
          xmax: response.boxes.value[0][i][2],
          ymax: response.boxes.value[0][i][3],
        },
      };
    });
    this.postProcessedOutputs = scores.sort((a: any, b: any) => {
      return b.score - a.score;
    });
  }
}
