import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiTextToImageInput = {
  prompt: string;
  num_steps?: number;
};

export type AiTextToImageOutput = Uint8Array;

export class AiTextToImage implements AiTask {
  private modelSettings: any;
  public inputs: AiTextToImageInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiTextToImageOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      properties: {
        prompt: {
          type: "string",
        },
        num_steps: {
          type: "integer",
          default: 20,
          maximum: 20,
        },
      },
      required: ["prompt"],
    },
    output: {
      type: "string",
      contentType: "image/png",
      format: "binary",
    },
  };

  constructor(inputs: AiTextToImageInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.String, [this.preProcessedInputs.prompt], {
        shape: [1],
        name: "input_text",
      }),
      new Tensor(TensorType.Int32, [this.preProcessedInputs.num_steps], {
        shape: [1],
        name: "num_steps",
      }),
    ];
  }

  postProcessing(response: any) {
    this.postProcessedOutputs = new Uint8Array(response.output_image.value);
  }
}
