import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiTextClassificationInput = {
  text: string;
};

export type AiTextClassificationOutput = {
  score?: number;
  label?: string;
}[];

export class AiTextClassification implements AiTask {
  private modelSettings: any;
  public inputs: AiTextClassificationInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiTextClassificationOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      properties: {
        text: {
          type: "string",
        },
      },
      required: ["text"],
    },
    output: {
      type: "array",
      contentType: "application/json",
      items: {
        type: "object",
        properties: {
          score: {
            type: "number",
          },
          label: {
            type: "string",
          },
        },
      },
    },
  };

  constructor(inputs: AiTextClassificationInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.String, [this.preProcessedInputs.text], {
        shape: [1],
        name: "input_text",
      }),
    ];
  }

  postProcessing(response: any) {
    this.postProcessedOutputs = [
      {
        label: "NEGATIVE",
        score: response.logits.value[0][0],
      },
      {
        label: "POSITIVE",
        score: response.logits.value[0][1],
      },
    ];
  }
}
