export enum TensorType {
  String = "str",
  Bool = "bool",
  Float16 = "float16",
  Float32 = "float32",
  // Float64 = "float64",
  Int16 = "int16",
  Int32 = "int32",
  Int64 = "int64",
  Int8 = "int8",
  Uint16 = "uint16",
  Uint32 = "uint32",
  Uint64 = "uint64",
  Uint8 = "uint8",
}

type TensorOpts = {
  shape?: number[];
  name?: string;
};

export type TensorsObject = {
  [name: string]: Tensor<any>;
};

const TypedArrayProto = Object.getPrototypeOf(Uint8Array);

function isArray(value: any) {
  return Array.isArray(value) || value instanceof TypedArrayProto;
}

// counts arrays, typed arrays, and arrays of typed arrays
function arrLength(obj: any) {
  return obj instanceof TypedArrayProto
    ? obj.length
    : obj.flat(Infinity).reduce((acc: any, cur: any) => acc + (cur instanceof TypedArrayProto ? cur.length : 1), 0);
}

function ensureShape(shape: number[], value: any) {
  if (shape.length === 0 && !isArray(value)) {
    // scalar value
    return;
  }

  const count = shape.reduce((acc, v) => {
    if (!Number.isInteger(v)) {
      throw new Error(`expected shape to be array-like of integers but found non-integer element "${v}"`);
    }
    return acc * v;
  }, 1);

  if (count != arrLength(value)) {
    throw new Error(
      `invalid shape: expected ${count} elements for shape ${shape} but value array has length ${value.length}`
    );
  }
}

function ensureType(type: TensorType, value: any) {
  if (isArray(value)) {
    value.forEach((v: any) => ensureType(type, v));
    return;
  }

  switch (type) {
    case TensorType.Bool: {
      if (typeof value === "boolean") {
        return;
      }
      break;
    }

    case TensorType.Float16:
    case TensorType.Float32: {
      // We can't make sure the value is a float (using !Number.isInteger) because
      // it's not possible in JS to transform a int into a float, which would
      // make our API unsuable.
      if (typeof value === "number") {
        return;
      }
      break;
    }

    case TensorType.Int8:
    case TensorType.Uint8:
    case TensorType.Int16:
    case TensorType.Uint16:
    case TensorType.Int32:
    case TensorType.Uint32: {
      if (Number.isInteger(value)) {
        return;
      }
      break;
    }

    case TensorType.Int64:
    case TensorType.Uint64: {
      if (typeof value === "bigint") {
        return;
      }
      break;
    }

    case TensorType.String: {
      if (typeof value === "string") {
        return;
      }
      break;
    }
  }

  throw new Error(`unexpected type "${type}" with value "${value}".`);
}

function serializeType(type: TensorType, value: any) {
  if (isArray(value)) {
    // Return as an array, transform TypedArray into regular arrays.
    return [...value].map((v) => serializeType(type, v));
  }
  switch (type) {
    case TensorType.String:
    case TensorType.Bool:
    case TensorType.Float16:
    case TensorType.Float32:
    case TensorType.Int8:
    case TensorType.Uint8:
    case TensorType.Int16:
    case TensorType.Uint16:
    case TensorType.Uint32:
    case TensorType.Int32: {
      return value;
    }

    case TensorType.Int64:
    case TensorType.Uint64: {
      return value.toString();
    }
  }

  throw new Error(`unexpected type "${type}" with value "${value}".`);
}

function deserializeType(type: TensorType, value: any) {
  if (isArray(value)) {
    return value.map((v) => deserializeType(type, v));
  }
  switch (type) {
    case TensorType.String:
    case TensorType.Bool:
    case TensorType.Float16:
    case TensorType.Float32:
    case TensorType.Int8:
    case TensorType.Uint8:
    case TensorType.Int16:
    case TensorType.Uint16:
    case TensorType.Uint32:
    case TensorType.Int32: {
      return value;
    }

    case TensorType.Int64:
    case TensorType.Uint64: {
      return BigInt(value);
    }
  }

  throw new Error(`unexpected type "${type}" with value "${value}".`);
}

export class Tensor<T extends TensorType> {
  type: T;
  value: any | any[];
  name: string | null;
  shape: number[];

  constructor(type: T, value: any | any[], opts: TensorOpts = {}) {
    this.type = type;
    this.value = value;

    ensureType(type, this.value);

    if (opts.shape === undefined) {
      if (isArray(this.value)) {
        this.shape = [arrLength(value)];
      } else {
        this.shape = [];
        // this.shape = [1];
        // this.value = [value];
      }
    } else {
      this.shape = opts.shape;
    }

    ensureShape(this.shape, this.value);

    this.name = opts.name || null;
  }

  static fromJSON(obj: any): Tensor<any> {
    const { type, shape, value, b64Value, name } = obj;
    const opts = { shape, name };

    if (b64Value !== undefined) {
      // `b64Value` is the legacy encoding for values that aren't representable in
      // JSON; old constellation-js client are still sending b64Value.
      const value = b64ToArray(b64Value, type)[0];
      return new Tensor(type, value, opts);
    } else {
      return new Tensor(type, deserializeType(type, value), opts);
    }
  }

  toJSON() {
    return {
      type: this.type,
      shape: this.shape,
      name: this.name,
      value: serializeType(this.type, this.value),
    };
  }
}

// Support for legacy value encoding
function b64ToArray(base64: string, type: string) {
  const byteString = atob(base64);
  const bytes = new Uint8Array(byteString.length);
  for (let i = 0; i < byteString.length; i++) {
    bytes[i] = byteString.charCodeAt(i);
  }
  const arrBuffer = new DataView(bytes.buffer).buffer;

  switch (type) {
    case "float32":
      return new Float32Array(arrBuffer);
    case "float64":
      return new Float64Array(arrBuffer);
    case "int32":
      return new Int32Array(arrBuffer);
    case "int64":
      return new BigInt64Array(arrBuffer);
    default:
      throw Error(`invalid data type for base64 input: ${type}`);
  }
}
