import { modelSettings } from "./catalog";
import { Tensor, TensorsObject } from "./tensor";

export const debugLog = (dd: Boolean, what: string, args: any) => {
  if (dd) {
    console.log(`\u001b[1m${what}`);
    if (args[0] !== false) {
      if (typeof args == "object" || Array.isArray(args)) {
        const json = JSON.stringify(args);
        console.log(json.length > 512 ? `${json.substring(0, 512)}...` : json);
      } else {
        console.log(args);
      }
    }
  }
};

export const parseInputs = (inputs: any) => {
  if (Array.isArray(inputs)) {
    return inputs.map((input) => input.toJSON());
  }
  if (inputs !== null && typeof inputs === "object") {
    return Object.keys(inputs).map((key) => {
      let tensor = inputs[key].toJSON();
      tensor.name = key;
      return tensor;
    });
  }
  throw new Error(`invalid inputs, must be Array<Tensor<any>> | TensorsObject`);
};

export const tensorByName = (result: any) => {
  const outputByName: TensorsObject = {};
  for (let i = 0, len = result.length; i < len; i++) {
    const tensor = Tensor.fromJSON(result[i]);
    const name = tensor.name || "output" + i;
    outputByName[name] = tensor;
  }
  return outputByName;
};

export const getModelSettings = (model: string, key?: string) => {
  const models = Object.keys(modelSettings);
  for (var m in models) {
    if (models[m] == model) {
      return key ? modelSettings[models[m]][key] : modelSettings[models[m]];
    }
  }
  return false;
};

// simplified code from https://github.com/rexxars/eventsource-parser

export interface EventSourceParser {
  feed(chunk: string): void;
  reset(): void;
}

export interface ParsedEvent {
  type: "event";
  event?: string;
  id?: string;
  data: string;
}

export interface ReconnectInterval {
  type: "reconnect-interval";
  value: number;
}

export type ParseEvent = ParsedEvent | ReconnectInterval;
export type EventSourceParseCallback = (event: ParseEvent) => void;

export class EventSourceParserStream extends TransformStream<string, ParsedEvent> {
  constructor() {
    let parser!: EventSourceParser;

    super({
      start(controller) {
        parser = createParser((event: any) => {
          if (event.type === "event") {
            controller.enqueue(event);
          }
        });
      },
      transform(chunk) {
        parser.feed(chunk);
      },
    });
  }
}

const BOM = [239, 187, 191];

function hasBom(buffer: string) {
  return BOM.every((charCode: number, index: number) => buffer.charCodeAt(index) === charCode);
}

export function createParser(onParse: EventSourceParseCallback): EventSourceParser {
  // Processing state
  let isFirstChunk: boolean;
  let buffer: string;
  let startingPosition: number;
  let startingFieldLength: number;

  // Event state
  let eventId: string | undefined;
  let eventName: string | undefined;
  let data: string;

  reset();
  return { feed, reset };

  function reset(): void {
    isFirstChunk = true;
    buffer = "";
    startingPosition = 0;
    startingFieldLength = -1;

    eventId = undefined;
    eventName = undefined;
    data = "";
  }

  function feed(chunk: string): void {
    buffer = buffer ? buffer + chunk : chunk;

    // Strip any UTF8 byte order mark (BOM) at the start of the stream.
    // Note that we do not strip any non - UTF8 BOM, as eventsource streams are
    // always decoded as UTF8 as per the specification.
    if (isFirstChunk && hasBom(buffer)) {
      buffer = buffer.slice(BOM.length);
    }

    isFirstChunk = false;

    // Set up chunk-specific processing state
    const length = buffer.length;
    let position = 0;
    let discardTrailingNewline = false;

    // Read the current buffer byte by byte
    while (position < length) {
      // EventSource allows for carriage return + line feed, which means we
      // need to ignore a linefeed character if the previous character was a
      // carriage return
      // @todo refactor to reduce nesting, consider checking previous byte?
      // @todo but consider multiple chunks etc
      if (discardTrailingNewline) {
        if (buffer[position] === "\n") {
          ++position;
        }
        discardTrailingNewline = false;
      }

      let lineLength = -1;
      let fieldLength = startingFieldLength;
      let character: string;

      for (let index = startingPosition; lineLength < 0 && index < length; ++index) {
        character = buffer[index];
        if (character === ":" && fieldLength < 0) {
          fieldLength = index - position;
        } else if (character === "\r") {
          discardTrailingNewline = true;
          lineLength = index - position;
        } else if (character === "\n") {
          lineLength = index - position;
        }
      }

      if (lineLength < 0) {
        startingPosition = length - position;
        startingFieldLength = fieldLength;
        break;
      } else {
        startingPosition = 0;
        startingFieldLength = -1;
      }

      parseEventStreamLine(buffer, position, fieldLength, lineLength);

      position += lineLength + 1;
    }

    if (position === length) {
      // If we consumed the entire buffer to read the event, reset the buffer
      buffer = "";
    } else if (position > 0) {
      // If there are bytes left to process, set the buffer to the unprocessed
      // portion of the buffer only
      buffer = buffer.slice(position);
    }
  }

  function parseEventStreamLine(lineBuffer: string, index: number, fieldLength: number, lineLength: number) {
    if (lineLength === 0) {
      // We reached the last line of this event
      if (data.length > 0) {
        onParse({
          type: "event",
          id: eventId,
          event: eventName || undefined,
          data: data.slice(0, -1), // remove trailing newline
        });

        data = "";
        eventId = undefined;
      }
      eventName = undefined;
      return;
    }

    const noValue = fieldLength < 0;
    const field = lineBuffer.slice(index, index + (noValue ? lineLength : fieldLength));
    let step = 0;

    if (noValue) {
      step = lineLength;
    } else if (lineBuffer[index + fieldLength + 1] === " ") {
      step = fieldLength + 2;
    } else {
      step = fieldLength + 1;
    }

    const position = index + step;
    const valueLength = lineLength - step;
    const value = lineBuffer.slice(position, position + valueLength).toString();

    if (field === "data") {
      data += value ? `${value}\n` : "\n";
    } else if (field === "event") {
      eventName = value;
    } else if (field === "id" && !value.includes("\u0000")) {
      eventId = value;
    } else if (field === "retry") {
      const retry = parseInt(value, 10);
      if (!Number.isNaN(retry)) {
        onParse({ type: "reconnect-interval", value: retry });
      }
    }
  }
}

export class ResultStream extends TransformStream {
  constructor() {
    super({
      transform(chunk, controller) {
        if (chunk.data === "[DONE]") {
          return;
        }

        try {
          const data = JSON.parse(chunk.data);
          controller.enqueue(data);
        } catch (err) {
          console.error(`failed to parse incoming data (${err.stack}): ${chunk.data}`);
          return;
        }
      },
    });
  }
}

export const getEventStream = (body: ReadableStream) => {
  const { readable, writable } = new TransformStream();
  const eventStream = (body ?? new ReadableStream())
    .pipeThrough(new TextDecoderStream())
    .pipeThrough(new EventSourceParserStream())
    .pipeThrough(new ResultStream());
  const reader = eventStream.getReader();
  const writer = writable.getWriter();
  const encoder = new TextEncoder();
  const write = async (data: string) => {
    await writer.write(encoder.encode(data));
  };
  return {
    readable,
    reader,
    writer,
    write,
  };
};

// end eventsource-parser

export const readStream = (
  body: ReadableStream,
  debug: boolean,
  ctx: any,
  tensorData: boolean,
  postProcessing: Function | false
) => {
  const { readable, reader, writer, write } = getEventStream(body);

  const waitUntil = ctx && ctx.waitUntil ? (f: any) => ctx.waitUntil(f()) : (f: any) => f();

  waitUntil(async () => {
    try {
      for (;;) {
        const { done, value } = await reader.read();
        if (done) {
          await write("data: [DONE]\n\n");
          break;
        }
        debugLog(debug, "stream response", value);
        if (tensorData) {
          const output = tensorByName(value.result);
          await write(`data: ${JSON.stringify(postProcessing ? postProcessing(output) : output)}\n\n`);
        } else {
          await write(`data: ${JSON.stringify(value)}\n\n`);
        }
      }
    } catch (e) {
      console.error(e.stack);
      await write("an unknown error occurred while streaming");
    }
    await writer.close();
  });

  return readable;
};
