import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";
import { resnetLabels } from "./data/labels";

export type AiImageClassificationInput = {
  image: number[];
};

export type AiImageClassificationOutput = {
  score?: number;
  label?: string;
}[];

export class AiImageClassification implements AiTask {
  private modelSettings: any;
  public inputs: AiImageClassificationInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiImageClassificationOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      oneOf: [
        { type: "string", format: "binary" },
        {
          type: "object",
          properties: {
            image: {
              type: "array",
              items: {
                type: "number",
              },
            },
          },
        },
      ],
    },
    output: {
      type: "array",
      contentType: "application/json",
      items: {
        type: "object",
        properties: {
          score: {
            type: "number",
          },
          label: {
            type: "string",
          },
        },
      },
    },
  };

  constructor(inputs: AiImageClassificationInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.Uint8, this.preProcessedInputs.image, {
        shape: [1, this.preProcessedInputs.image.length],
        name: "input",
      }),
    ];
  }

  postProcessing(response: any) {
    const labels = [];
    const scores = response.output.value[0];
    for (var s in scores) labels.push({ label: resnetLabels[s], score: scores[s] });
    labels.sort((a, b) => {
      return b.score - a.score;
    });
    this.postProcessedOutputs = labels.slice(0, 5);
  }
}
