import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiTranslationInput = {
  text: string;
  target_lang: string;
  source_lang?: string;
};

export type AiTranslationOutput = {
  translated_text?: string;
};

export class AiTranslation implements AiTask {
  private modelSettings: any;
  public inputs: AiTranslationInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiTranslationOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      properties: {
        text: {
          type: "string",
        },
        source_lang: {
          type: "string",
          default: "en",
        },
        target_lang: {
          type: "string",
        },
      },
      required: ["text", "target_lang"],
    },
    output: {
      type: "object",
      contentType: "application/json",
      properties: {
        translated_text: {
          type: "string",
        },
      },
    },
  };

  constructor(inputs: AiTranslationInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.String, [this.preProcessedInputs.text], {
        shape: [1, 1],
        name: "text",
      }),
      new Tensor(TensorType.String, [this.preProcessedInputs.source_lang || "en"], {
        shape: [1, 1],
        name: "source_lang",
      }),
      new Tensor(TensorType.String, [this.preProcessedInputs.target_lang], {
        shape: [1, 1],
        name: "target_lang",
      }),
    ];
  }

  postProcessing(response: any) {
    this.postProcessedOutputs = { translated_text: response.name.value[0] };
  }
}
