/*
 * Model settings:
 *
 * experimental: excluded from config-api and devdocs if true
 * preProcessingArgs: per-model pre-processing arguments
 * inputsDefaults: per-model inputs defaults
 * inputsDefaultsStream: per-model inputs defaults when stream is used
 *
 */

const tgiPostProc = (response: any, ignoreTokens?: any) => {
  let r = response["generated_text"].value[0];
  if (ignoreTokens) {
    for (var i in ignoreTokens) r = r.replace(ignoreTokens[i], "");
  }
  return r;
};

