import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";
import { generateTgTemplate } from "../templates";

export type RoleScopedChatInput = {
  role: string;
  content: string;
};

export type AiTextGenerationInput = {
  prompt?: string;
  raw?: boolean;
  stream?: boolean;
  max_tokens?: number;
  messages?: RoleScopedChatInput[];
};

export type AiTextGenerationOutput =
  | {
      response?: string;
    }
  | ReadableStream;

export class AiTextGeneration implements AiTask {
  private modelSettings: any;
  public inputs: AiTextGenerationInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiTextGenerationOutput | ReadableStream;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      oneOf: [
        {
          properties: {
            prompt: {
              type: "string",
              maxLength: 4096,
            },
            raw: {
              type: "boolean",
              default: false,
            },
            stream: {
              type: "boolean",
              default: false,
            },
            max_tokens: {
              type: "integer",
              default: 256,
            },
          },
          required: ["prompt"],
        },
        {
          properties: {
            messages: {
              type: "array",
              items: {
                type: "object",
                properties: {
                  role: {
                    type: "string",
                  },
                  content: {
                    type: "string",
                    maxLength: 4096,
                  },
                },
                required: ["role", "content"],
              },
            },
            stream: {
              type: "boolean",
              default: false,
            },
            max_tokens: {
              type: "integer",
              default: 256,
            },
          },
          required: ["messages"],
        },
      ],
    },
    output: {
      oneOf: [
        {
          type: "object",
          contentType: "application/json",
          properties: {
            response: {
              type: "string",
            },
          },
        },
        {
          type: "string",
          contentType: "text/event-stream",
          format: "binary",
        },
      ],
    },
  };

  constructor(inputs: AiTextGenerationInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings || {
      experimental: true,
      inputsDefaultsStream: {
        max_tokens: 512,
      },
      inputsDefaults: {
        max_tokens: 256,
      },
      preProcessingArgs: {
        promptTemplate: "inst",
        defaultContext: "",
      },
    };
  }

  preProcessing() {
    // model specific inputs defaults, if any
    if (this.inputs.stream && this.modelSettings.inputsDefaultsStream) {
      this.inputs = { ...this.modelSettings.inputsDefaultsStream, ...this.inputs };
    } else if (this.modelSettings.inputsDefaults) {
      this.inputs = { ...this.modelSettings.inputsDefaults, ...this.inputs };
    }

    let prompt: string = "";
    // prompt syntax
    if (this.inputs.messages === undefined) {
      if (this.inputs.raw == true) {
        prompt = this.inputs.prompt;
      } else {
        prompt = generateTgTemplate(
          [
            { role: "system", content: this.modelSettings.preProcessingArgs.defaultContext },
            { role: "user", content: this.inputs.prompt },
          ],
          this.modelSettings.preProcessingArgs.promptTemplate
        );
      }
    } else {
      // scoped inputs
      prompt = generateTgTemplate(this.inputs.messages, this.modelSettings.preProcessingArgs.promptTemplate);
    }
    this.preProcessedInputs = prompt;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.String, [this.preProcessedInputs], {
        shape: [1],
        name: "INPUT_0",
      }),
      new Tensor(TensorType.Uint32, [this.inputs.max_tokens], {
        // sequence length
        shape: [1],
        name: "INPUT_1",
      }),
    ];
  }

  postProcessing(response: any) {
    if (this.modelSettings.postProcessingFunc) {
      this.postProcessedOutputs = { response: this.modelSettings.postProcessingFunc(response) };
    } else {
      this.postProcessedOutputs = { response: response.name.value[0] };
    }
  }

  postProcessingStream(response: any) {
    if (this.modelSettings.postProcessingFuncStream) {
      return { response: this.modelSettings.postProcessingFuncStream(response) };
    } else {
      return { response: response.name.value[0] };
    }
  }
}
