import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiSentenceSimilarityInput = {
  source: string;
  sentences: string[];
};

export type AiSentenceSimilarityOutput = number[];

export class AiSentenceSimilarity implements AiTask {
  public inputs: AiSentenceSimilarityInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiSentenceSimilarityOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      properties: {
        source: {
          type: "string",
        },
        sentences: {
          type: "array",
          items: {
            type: "string",
          },
        },
      },
      required: ["source", "sentences"],
    },
    output: {
      type: "array",
      contentType: "application/json",
      items: {
        type: "number",
      },
    },
  };

  constructor(inputs: AiSentenceSimilarityInput) {
    this.inputs = inputs;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.String, [this.preProcessedInputs.source], {
        shape: [1],
        name: "source_sentence",
      }),
      new Tensor(TensorType.String, this.preProcessedInputs.sentences, {
        shape: [this.preProcessedInputs.sentences.length],
        name: "sentences",
      }),
    ];
  }

  postProcessing(response: any) {
    this.postProcessedOutputs = response.scores.value;
  }
}
