import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiSpeechRecognitionInput = {
  audio: number[];
};

export type AiSpeechRecognitionOutput = {
  text?: string;
};

export class AiSpeechRecognition implements AiTask {
  private modelSettings: any;
  public inputs: AiSpeechRecognitionInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiSpeechRecognitionOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      oneOf: [
        { type: "string", format: "binary" },
        {
          type: "object",
          properties: {
            audio: {
              type: "array",
              items: {
                type: "number",
              },
            },
          },
        },
      ],
    },
    output: {
      type: "object",
      contentType: "application/json",
      properties: {
        text: {
          type: "string",
        },
        word_count: {
          type: "number",
        },
        words: {
          type: "array",
          items: {
            type: "object",
            properties: {
              word: {
                type: "string",
              },
              start: {
                type: "number",
              },
              end: {
                type: "number",
              },
            },
          },
        },
      },
      required: ["text"],
    },
  };

  constructor(inputs: AiSpeechRecognitionInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(TensorType.Uint8, this.preProcessedInputs.audio, {
        shape: [1, this.preProcessedInputs.audio.length],
        name: "audio",
      }),
    ];
  }

  postProcessing(response: any) {
    if (this.modelSettings.postProcessingFunc) {
      this.postProcessedOutputs = this.modelSettings.postProcessingFunc(response);
    } else {
      this.postProcessedOutputs = { text: response.name.value[0].trim() };
    }
  }
}
