// Helper function to get the last 10 image keys
// You need to implement this based on your KV store's capabilities
async function getLastTenImageKeys(kvStore): Promise<string[]> {
  // Assuming the KV store has a list of keys ordered by insertion time
  const keys = await kvStore.list({ limit: 100, reverse: false });
  const randomKeys = keys.keys
    .map(entry => entry.name)
    .sort(() => Math.random() - 0.5)
    .slice(0, 25);

  return randomKeys;
}

