function restructureSentiment([neg, pos], comment) {
  console.log(neg, pos, comment)
  // if (neg.score > pos.score) {
  if (neg.score > pos.score) {
    return {
      text: comment,
      sentiment: 'NEGATIVE',
      score: neg.score
    }
  } else {
    return {
      text: comment,
      sentiment: 'POSITIVE',
      score: pos.score
    }
  }
}

