import { Tensor, TensorType } from "../tensor";
import { AiTask } from "../ai";

export type AiTextEmbeddingsInput = {
  text: string | string[];
};

export type AiTextEmbeddingsOutput = {
  shape: number[];
  data: number[][];
};

const chunkArray = (arr: any, size: number) =>
  arr.length > size ? [arr.slice(0, size), ...chunkArray(arr.slice(size), size)] : [arr];

export class AiTextEmbeddings implements AiTask {
  private modelSettings: any;
  public inputs: AiTextEmbeddingsInput;
  public preProcessedInputs: any;
  public postProcessedOutputs: AiTextEmbeddingsOutput;
  public tensors: Array<Tensor<any>>;
  // run ./scripts/gen-validators.ts if you change the schema
  public schema = {
    input: {
      type: "object",
      properties: {
        text: {
          oneOf: [
            { type: "string" },
            {
              type: "array",
              items: {
                type: "string",
              },
              maxItems: 100,
            },
          ],
        },
      },
      required: ["text"],
    },
    output: {
      type: "object",
      contentType: "application/json",
      properties: {
        shape: {
          type: "array",
          items: {
            type: "number",
          },
        },
        data: {
          type: "array",
          items: {
            type: "array",
            items: {
              type: "number",
            },
          },
        },
      },
    },
  };

  constructor(inputs: AiTextEmbeddingsInput, modelSettings: any) {
    this.inputs = inputs;
    this.modelSettings = modelSettings;
  }

  preProcessing() {
    this.preProcessedInputs = this.inputs;
  }

  generateTensors() {
    this.tensors = [
      new Tensor(
        TensorType.String,
        Array.isArray(this.preProcessedInputs.text) ? this.preProcessedInputs.text : [this.preProcessedInputs.text],
        {
          shape: [
            Array.isArray(this.preProcessedInputs.text)
              ? this.preProcessedInputs.text.length
              : [this.preProcessedInputs.text].length,
          ],
          name: "input_text",
        }
      ),
    ];
  }

  postProcessing(response: any) {
    if (this.modelSettings.postProcessingFunc) {
      this.postProcessedOutputs = this.modelSettings.postProcessingFunc(response);
    } else {
      this.postProcessedOutputs = {
        shape: response.embeddings.shape,
        data: response.embeddings.value,
      };
    }
  }
}
