import { Tensor } from "./tensor";
import { InferenceSession, SessionOptions } from "./session";
import { readStream, getModelSettings, debugLog } from "./tools";
import { modelMappings, addModel } from "./catalog";

export type AiTask = {
  tensors: Array<Tensor<any>>;
  preProcessedInputs: any;
  postProcessedOutputs: any;
  schema: any;
  preProcessing: Function;
  generateTensors: Function;
  postProcessing: Function;
  postProcessingStream?: Function;
};

type AiOptions = {
  debug?: boolean;
  apiGateway?: Boolean;
  apiAccount?: string;
  apiToken?: string;
  sessionOptions?: SessionOptions;
};

type ModelMappings = typeof modelMappings;

type GetModelName<T> = {
  [K in keyof T]: T[K] extends { models: readonly (infer U)[] } ? U : never;
}[keyof T];

type ModelName = GetModelName<ModelMappings>;

type GetModelClass<M extends ModelName, T> = {
  [K in keyof T]: T[K] extends { models: readonly string[]; class: infer C }
    ? M extends T[K]["models"][number]
      ? C
      : never
    : never;
}[keyof T];

type ConstructorParametersForModel<M extends ModelName> = ConstructorParameters<GetModelClass<M, ModelMappings>>[0];

export class Ai {
  private binding: any;
  private options: AiOptions;
  private task: AiTask;

  constructor(binding: any, options: AiOptions = {}) {
    this.binding = binding;
    this.options = options;
  }

  addModel(task: string, model: string, settings: any) {
    addModel(task, model, settings);
  }

  async run<M extends ModelName>(model: M, inputs: ConstructorParametersForModel<M>): Promise<any> {
    const tasks = Object.keys(modelMappings);

    for (var t in tasks) {
      if (modelMappings[tasks[t]].models.indexOf(model) !== -1) {
        const settings = getModelSettings(model);
        const sessionOptions = this.options.sessionOptions || {};
        this.task = new modelMappings[tasks[t]].class(inputs, settings);
        debugLog(this.options.debug, "input", inputs);
        // code to handle using the API Gateway instead
        if (this.options.apiGateway) {
          const fetchOptions = {
            method: "POST",
            body: JSON.stringify(inputs),
            headers: {
              authorization: `Bearer ${this.options.apiToken}`,
              "content-type": "application/json",
            },
          };
          const res = await fetch(
            `https://api.cloudflare.com/client/v4/accounts/${this.options.apiAccount}/ai/run/${model}`,
            fetchOptions
          );
          if (!res.ok) {
            throw new Error(await res.text());
          }
          // normal
          if (res.headers.get("content-type") == "application/json") {
            const { result } = await res.json();
            return result;
          } else if (res.headers.get("content-type") == "text/event-stream") {
            return readStream(res.body, this.options.debug, sessionOptions.ctx, false, false);
          } else {
            // binary
            const blob = await res.blob();
            return blob;
          }
        } else {
          // Normal binding
          this.task.preProcessing();
          debugLog(this.options.debug, "pre-processed inputs", this.task.preProcessedInputs);
          this.task.generateTensors();
          debugLog(this.options.debug, "input tensors", this.task.tensors);
          const session = new InferenceSession(this.binding, model, {
            ...{ debug: this.options.debug ? true : false },
            ...sessionOptions,
          });
          if ((inputs as any).stream) {
            debugLog(this.options.debug, "streaming", false);
            return await session.run(this.task.tensors, {
              stream: true,
              postProcessing: (r: any) => {
                return this.task.postProcessingStream(r);
              },
            });
          } else {
            const response = await session.run(this.task.tensors);
            debugLog(this.options.debug, "response", response);
            this.task.postProcessing(response, sessionOptions.ctx);
            debugLog(this.options.debug, "post-processed response", this.task.postProcessedOutputs);
            return this.task.postProcessedOutputs;
          }
        }
      }
    }

    throw new Error(`No such model ${model} or task`);
  }
}
